import React from "react"
import { Link } from "gatsby"
import images from "../images/images"

const Navbar = ({ data }) => {
    return (
        <header className="relative z-10 py-11">
            <div className="container px-4 mx-auto">
                <div className="flex items-center">
                    <Link to="/"><img src={images.logo} alt=""/></Link>
                    <ul className="flex ml-auto">
                        {
                            data.map((item, i) => 
                                <li key={i} className="pl-9">
                                    {item.url.startsWith("http") ? 
                                        <a href={item.url} className="text-sm text-white font-ArcoSemi transition-all duration-100 hover:text-yellow-500">{item.name}</a>
                                        : <Link to={item.url} className="text-sm text-white font-ArcoSemi transition-all duration-100 hover:text-yellow-500">{item.name}</Link>
                                    }
                                </li>
                            )
                        }
                    </ul>
                </div>
            </div>
        </header>
    )
}

export default Navbar
